.mbtiLayout {
  width: 100vw;
  height: calc(var(--vh, 1vh) * 100);
  font-size: 14px;
  display: flex;
  justify-content: center;
  background-image: linear-gradient(
    to bottom,
    rgb(77, 185, 248),
    rgb(231, 214, 248)
  );
}

.startPageLayout {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  color: white;
  animation-name: fadeIn;
  animation-duration: 1.5s;
}

.startLogo {
  display: inline-block;
  font-size: 40px;
  font-weight: 650;
  pointer-events: none;
  color: rgb(60, 167, 255);
  background-color: white;
  border-radius: 50%;
  padding: 20px 40px;
  margin-bottom: 50px;
  box-shadow: 1.5px 1.5px rgba(46, 160, 253, 0.288);
}

.startLogo:before {
  content: "";
  position: absolute;
  background: white;
  height: 20px;
  width: 20px;
  border-radius: 10px;
  bottom: calc(var(--vh, 1vh) * 46);
  right: 50vw;
  box-shadow: 1.5px 1.5px rgba(46, 160, 253, 0.288);
}

.startLogo:after {
  content: "";
  position: absolute;
  background: white;
  height: 10px;
  width: 10px;
  border-radius: 5px;
  bottom: calc(var(--vh, 1vh) * 44);
  right: 50vw;
  box-shadow: 1.5px 1.5px rgba(46, 160, 253, 0.288);
}

.startButton {
  font-size: 25px;
  cursor: pointer;
  font-weight: 550;
  block-size: 30px;
}

.startButton:hover {
  font-size: 30px;
  color: rgb(60, 167, 255);
  text-shadow: 1.5px 1.5px rgba(46, 160, 253, 0.288);
}

@keyframes fadeIn {
  from {
    opacity: 0.2;
  }
  to {
    opacity: 1;
  }
}
